import React, { memo } from 'react'
import DefaultTemplate from '../template'
import type { TemplateProps } from '../template'
import type { KcProps } from 'keycloakify'
import type { KcContextBase } from 'keycloakify'
import { clsx } from 'keycloakify/lib/tools/clsx'
import type { I18n } from '../../i18n'

export type LoginUpdatePasswordProps = KcProps & {
  kcContext: KcContextBase.LoginUpdatePassword
  i18n: I18n
  doFetchDefaultThemeResources?: boolean
  Template?: (props: TemplateProps) => JSX.Element | null
}

const LoginUpdatePassword = memo((props: LoginUpdatePasswordProps) => {
  const {
    kcContext,
    i18n,
    doFetchDefaultThemeResources = true,
    Template = DefaultTemplate,
    ...kcProps
  } = props

  const { msg, msgStr } = i18n

  const { url, messagesPerField, isAppInitiatedAction, username } = kcContext

  return (
    <Template
      {...{ kcContext, i18n, doFetchDefaultThemeResources, ...kcProps }}
      headerNode={
        <>
          <h3 style={{ color: '#6639F4', fontWeight: 700, fontSize: '0.875rem' }}>
            Vamos criar uma nova senha
          </h3>
          <h1 style={{ color: 'black', fontWeight: 700, fontSize: '2rem' }}>
            {msg('updatePasswordTitle')}
          </h1>
          <h3 style={{ color: '#333333', fontWeight: 700, fontSize: '1rem' }}>
            Instruções para a senha
          </h3>
          <ul
            style={{
              color: '#333333',
              fontWeight: 500,
              fontSize: '0.875rem',
              padding: '0rem 1.5rem',
            }}
          >
            <li style={{ padding: '0.3rem 0' }}>
              Deve conter pelo menos <strong> 8 caracteres</strong>
            </li>
            <li style={{ padding: '0.3rem 0' }}>
              Deve conter pelo menos <strong>1 letra maiúscula</strong>
            </li>
            <li style={{ padding: '0.3rem 0' }}>
              Deve conter pelo menos <strong>1 letra minúscula</strong>
            </li>
            <li style={{ padding: '0.3rem 0' }}>
              Deve conter pelo menos <strong>1 número(s)</strong>
            </li>
            <li style={{ padding: '0.3rem 0' }}>
              Deve conter pelo menos <strong>1 caractere especial</strong>
            </li>
          </ul>
        </>
      }
      formNode={
        <form
          id='kc-passwd-update-form'
          className={clsx(kcProps.kcFormClass)}
          action={url.loginAction}
          method='post'
        >
          <input
            type='text'
            id='username'
            name='username'
            value={username}
            readOnly={true}
            autoComplete='username'
            style={{ display: 'none' }}
          />
          <input
            type='password'
            id='password'
            name='password'
            autoComplete='current-password'
            style={{ display: 'none' }}
          />

          <div
            className={clsx(
              kcProps.kcFormGroupClass,
              messagesPerField.printIfExists('password', kcProps.kcFormGroupErrorClass)
            )}
          >
            <div className={clsx(kcProps.kcLabelWrapperClass)}>
              <label htmlFor='password-new' className={clsx(kcProps.kcLabelClass)}>
                {msg('passwordNew')}
              </label>
            </div>
            <div className={clsx(kcProps.kcInputWrapperClass)}>
              <input
                type='password'
                id='password-new'
                name='password-new'
                autoFocus
                autoComplete='new-password'
                className={clsx(kcProps.kcInputClass)}
              />
            </div>
          </div>

          <div
            className={clsx(
              kcProps.kcFormGroupClass,
              messagesPerField.printIfExists('password-confirm', kcProps.kcFormGroupErrorClass)
            )}
          >
            <div className={clsx(kcProps.kcLabelWrapperClass)}>
              <label htmlFor='password-confirm' className={clsx(kcProps.kcLabelClass)}>
                {msg('passwordConfirm')}
              </label>
            </div>
            <div className={clsx(kcProps.kcInputWrapperClass)}>
              <input
                type='password'
                id='password-confirm'
                name='password-confirm'
                autoComplete='new-password'
                className={clsx(kcProps.kcInputClass)}
              />
            </div>
          </div>

          <div className={clsx(kcProps.kcFormGroupClass)}>
            <div id='kc-form-options' className={clsx(kcProps.kcFormOptionsClass)}>
              <div className={clsx(kcProps.kcFormOptionsWrapperClass)}>
                {isAppInitiatedAction && (
                  <div className='checkbox'>
                    <label>
                      <input
                        type='checkbox'
                        id='logout-sessions'
                        name='logout-sessions'
                        value='on'
                        checked
                      />
                      {msgStr('logoutOtherSessions')}
                    </label>
                  </div>
                )}
              </div>
            </div>

            <div id='kc-form-buttons' className={clsx(kcProps.kcFormButtonsClass)}>
              {isAppInitiatedAction ? (
                <>
                  <input
                    className={clsx(
                      kcProps.kcButtonClass,
                      kcProps.kcButtonPrimaryClass,
                      kcProps.kcButtonLargeClass
                    )}
                    type='submit'
                    defaultValue={msgStr('doSubmit')}
                  />
                  <button
                    className={clsx(
                      kcProps.kcButtonClass,
                      kcProps.kcButtonDefaultClass,
                      kcProps.kcButtonLargeClass
                    )}
                    type='submit'
                    name='cancel-aia'
                    value='true'
                  >
                    {msg('doCancel')}
                  </button>
                </>
              ) : (
                <input
                  className={clsx(
                    kcProps.kcButtonClass,
                    kcProps.kcButtonPrimaryClass,
                    kcProps.kcButtonBlockClass,
                    kcProps.kcButtonLargeClass
                  )}
                  type='submit'
                  defaultValue={msgStr('doSubmit')}
                />
              )}
            </div>
          </div>
        </form>
      }
    />
  )
})

export default LoginUpdatePassword
